<template lang="pug">
  .user(:class="type === 'home' ? 'user--home' : ''")
    .user__photo-wrap
      img.user__photo(src="../assets/img/content/myphoto-small.jpg", alt="")

    .user__name Наталия Петкевич
    .user__desc личный сайт веб-разработчика

    ul(v-show="type === 'home'").user__socials.socials
      li
        a(href="https://vk.com/natalia_pt").socials__item
          img.socials__icon.socials__icon--vk(src="../assets/img/icons/vk-color.svg", alt="")
      li
        a(href="https://www.linkedin.com/in/natalia-pt").socials__item
          img.socials__icon.socials__icon--in(src="../assets/img/icons/in-color.svg", alt="")
    ul(v-show="type === 'home'").user__menu
      router-link(v-for="(link, i) in pages", :key="i", tag="li", :to="link.href").user-menu__item {{ link.text }}

</template>
<style lang="scss">
  @import '../assets/sass/vars';
  @import '../assets/sass/mixins';
  .user {
    text-align: center;
    width: 380px;
    border-radius: 5px;
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 50;
    @include laptop {
      max-width: 348px;
    }
    @include tablets {
      max-width: 300px;
    }
  }
  .user--home {
    background-color: rgba(#fcfcfc, .7);
  }
  .user__photo-wrap {
    border-radius: 50%;
    overflow: hidden;
    width: 150px;
    height: 150px;
    margin: 40px auto 10px;
  }
  .user__photo {
    width: 100%;
    height: auto;
  }
  .user__socials {
    margin-bottom: 40px;
  }
  .socials {
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  .socials__icon {
    width: 36px;
    height: 20px;
    cursor: pointer;
    @include laptop {
      width: 25px;
      height: 14px;
    }
    &:hover {
      opacity: .7;
    }
  }
  .socials__icon--vk {
    width: 32px;
    height: 16px;
  }
  .socials__icon--in {
    width: 30px;
    height: 21px;
  }
  .socials__item:first-child {
    margin-right: 15px;
  }
  .user__menu {
    display: flex;
  }
  .user-menu__item {
    flex-basis: 50%;
    text-align: center;
    padding: 12px;
    color: #fff;
    font-size: 16px;
    font-family: 'robotomedium';
    background-color: rgba($blue, .7);
    display: flex;
    justify-content: center;
    cursor: pointer;
    &:first-child {
      border-right: 2px solid rgba(#fcfcfc, .7);
    }
    &:hover {
      background-color: $blue;
    }
    @include tablets {
      font-size: 14px;
    }
  }
  .user__name {
    font-size: 28px;
    margin-bottom: 4px;
    font-family: 'robotomedium';
    color: $dark-blue;
    @include tablets {
      font-size: 21px;
    }
  }
  .user__desc {
    font-size: 16px;
    margin-bottom: 30px;
    font-family: 'robotoregular';
    color: $dark-blue;
    @include tablets {
      font-size: 14px;
    }
  }
</style>
<script>
export default {
  data () {
    return {
      pages: [
        {href: '/about', text: 'Обо мне'},
        {href: '/works', text: 'Мои работы'}
      ],
      socials: [
        {href: 'https://vk.com/natalia_pt/', src: '../../assets/img/svg/vk.svg'},
        {href: 'https://www.linkedin.com/in/natalia-pt/', src: '../../assets/img/svg/linkedIn.svg'}
      ]
    }
  },
  props: ['type'],
  methods: {
    // getImg (i) {
    // console.log(111, '../../assets/img/parallax/Layer' + '' + 11-i + '.png')
    // return `../../assets/img/parallax/Layer ${11 - i}.png`
    // }
  }
}
</script>



