<template lang="pug">
  footer.footer
    .container
      .footer__section
        ul.footer__menu.menu
          router-link(v-for="(link, i) in pages", :key="i", tag="li", :to="link.href").footer__menu-item {{ link.text }}
        ul.footer__socials.socials
          li
            a(href="https://vk.com/natalia_pt").footer__social-item
              img.footer__icon(src="../assets/img/icons/vk_round.svg", alt="")
          li
            a(href="https://www.linkedin.com/in/natalia-pt").footer__social-item
              img.footer__icon(src="../assets/img/icons/in_round.svg", alt="")
      .footer__section
        span.footer__copyright Я веб-разработчик из Гродно, и это мой сайт-портфолио.
        span.footer__copyright  &copy; Н. А. Петкевич, 2017

</template>
<style lang="scss">
  @import '../assets/sass/vars';
  @import '../assets/sass/mixins';
  footer {
    flex-grow: 0;
    width: 100%;
  }
  .footer {
    background-color: rgba($dark-blue, .7);
  }
  .footer__section {
    border-bottom: 1px solid rgba(#fff, .1);
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include laptop {
      flex-direction: column;
      align-items: center;
    }
  }
  .footer__menu {
    @include laptop {
      margin-bottom: 15px;
    }
  }
  .footer__menu-item {
    color: rgba(#fff, .8);
    font-size: 16px;
    font-family: 'robotomedium';
    margin-right: 40px;
    cursor: pointer;
    &:hover {
      color: #fff;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  .footer__socials {
    width: 100px;
    display: flex;
    justify-content: space-between;
  }
  .footer__social-item:hover {
    opacity: .7;
  }
  .footer__icon {
    width: 35px;
    height: 35px;
  }
  .footer__copyright {
    color: rgba(#fff, .5);
    font-size: 16px;
    font-family: 'robotolight';
    @include laptop {
      margin-bottom: 15px;
    }
  }
</style>
<script>
export default {
  data () {
    return {
      pages: [
        { href: '/about', text: 'Обо мне' },
        { href: '/works', text: 'Мои работы' }
      ]
    }
  }
}
</script>



