<template lang="pug">
  section.section-hero(ref="section")
    //.section-hero__bg(ref="bg")
      img(src="../assets/img/bg/portfolio.jpg", alt="")

    .section-hero__title(ref="title")
      img(src="../assets/img/decor/portfolio-header.png", alt="")

    Block-user(:type="'about'", ref="block")

    .section-hero__arrow.arrow(@click="scroll")
      img(src="../assets/img/icons/arrow_down.svg", alt="").arrow__icon

    .section-hero__triangle
      .section-hero__triangle-svg
        svg.triangle(version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" xml:space="preserve" viewBox="0 0 1000 100" preserveAspectRatio="none")
          polygon(points="0,1000 0,0 1000,100")

      .section-hero__triangle-svg
        svg.triangle(version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" xml:space="preserve" viewBox="0 0 1000 100" preserveAspectRatio="none")
          polygon(points="0,100 1000,0 1000,100")

</template>
<style lang="scss">
  @import '../assets/sass/vars';
  @import '../assets/sass/mixins';
  .section-hero {
    // background: url("../assets/img/bg/portfolio.jpg") no-repeat center;
    background-color: $blue;
    position: relative;
    height: 100vh;
    transform-style: preserve-3d;
    overflow: hidden;
    & .user__name {
      color: #fff;
      font-size: 35px;
      @include tablets {
        font-size: 21px;
      }
    }
    & .user__desc {
      color: #fff;
      @include tablets {
        font-size: 14px;
      }
    }
  }
  .section-hero__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 1270px;
  }
  .icon-bg {
    position: absolute;
    font-size: 35px;
    color: rgba(#fff, .3);
    z-index: 0;
  }
  .section-hero__title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 50;
    width: 80%;
    img {
      width: 100%;
    }
  }
  .section-hero__arrow {
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    opacity: .7;
    z-index: 100;
    &:hover {
      opacity: 1;
    }
  }
  .section-hero__triangle {
    position: absolute;
    bottom: 0%;
    width: 100%;
    display: flex;
    z-index: 0;
  }
  .section-hero__triangle-svg {
    display: inline-block;
    font-size: 0;
    width: 50%;
  }
  .triangle {
    width: 100%;
    height: 100px;
    fill: $light;
    @include desktop {
      height: 70px;
    }
    @include tablets {
      height: 30px;
    }
  }
</style>
<script>
import BlockUser from '../components/Block-user.vue'
export default {
  data () {
    return {
    }
  },
  components: {
    BlockUser
  },
  methods: {
    animateBg () {
      const section = this.$refs.section
      let icon = document.createElement('I')
      let classes = ['icon-zoom',
        'icon-plus',
        'icon-home-outline',
        'icon-wi-fi-outline',
        'icon-thumbs-up',
        'icon-lightbulb',
        'icon-device-desktop',
        'icon-headphones1']
      let maxX = document.documentElement.clientWidth
      let maxY = document.documentElement.clientHeight

      let getRandom = (min, max) => {
        let rand = min - 0.5 + Math.random() * (max - min + 1)
        rand = Math.round(rand)
        return rand
      }

      let getCoords = () => {
        return {
          top: getRandom(0, maxY),
          left: getRandom(0, maxX)
        }
      }

      let add = () => {
        let i = getRandom(0, classes.length - 1)
        let className = classes[i]

        icon.classList.add(className)
        icon.classList.add('icon-bg')
        icon.classList.add('floating')
        icon.style.top = `${getCoords().top}px`
        icon.style.left = `${getCoords().left}px`
        icon.style.animationDelay = `${getRandom(0, 2)}s`

        section.appendChild(icon)
      }
      add()
    },
    scroll () {
      this.$emit('scroll')
    }
  },
  mounted () {
    for (let i = 0; i < 80; i++) {
      this.animateBg()
    }
  }
}
</script>



