<template lang="pug">
  div
    Header
    TopSection(@scroll="scroll")
    section.section-slider(ref="content")
      .section-slider__title
        .title Мои работы
        .container.works
          .work(v-for="(item, i) in works", :key="i")
            .slider__descr
              .slider__descr-info
                .slider__descr-title
                  .title.title--small {{ item.name }}
                .slider__descr-skills {{ item.descr }}
                .slider__descr-btns
                  a(:href="item.refSite").btn.btn--link Посмотреть сайт
                  a(v-if="item.refRepo", :href="item.refRepo").btn.btn--link Ссылка на репозиторий

      //.slider
        .slider__descr
          .slider__descr-info
            .slider__descr-title
              .title.title&#45;&#45;small {{ activeWork.name }}
            .slider__descr-skills {{ activeWork.descr }}
            .slider__descr-btns
              a(:href="activeWork.refSite").btn.btn&#45;&#45;link Посмотреть сайт
              a(v-if="activeWork.refRepo", :href="activeWork.refRepo").btn.btn&#45;&#45;link Ссылка на репозиторий

        .slider__display
          img(:src="activeWork.img").slider__display-img
        .slider__btns
          .slider__btn.slider__btn-left
            img(src="../assets/img/slider/work-1.png")
          .slider__btn.slider__btn-right
            img(src="../assets/img/slider/work-1.png")
    Footer

</template>
<style lang="scss">
  @import '../assets/sass/vars';
  @import '../assets/sass/mixins';
  .section-slider {
    background-color: $light;
    padding-top: 50px;
    //height: 100vh;
    min-height: 650px;
  }
  .section-slider__title {
    text-align: center;
    padding-bottom: 30px;
  }
  .slider {
    height: 80%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    overflow: hidden;
    @include tablets {
      flex-wrap: nowrap;
    }
  }
  .slider__descr-info {
    margin: auto;
  }
  .slider__descr {
    height: 100%;
    display: flex;
    margin: auto;
    @include tablets {
      height: auto;
    }
  }
  .title--small {
    font-size: 30px;
    @include tablets {
      font-size: 21px;
    }
  }
  .slider__descr-skills {
    font-family: 'robotomedium';
    font-size: 16px;
    color: $blue;
    border-top: 1px solid $blue;
    border-bottom: 1px solid $blue;
    padding: 20px 0;
    text-align: center;
    margin-bottom: 30px;
    @include tablets {
      font-size: 14px;
    }
  }
  .slider__descr-title {
    margin-bottom: 20px;
    text-align: center;
  }
  .slider__display {
    flex: 4;
    display: flex;
    margin-bottom: 30px;
    overflow: hidden;
    @include tablets {
      order: -1;
    }
  }
  .slider__display-img {
    margin: auto;
    display: block;
    max-height: 500px;
    max-width: 500px;
    @include tablets {
      max-width: 220px;
      max-height: 300px;
    }
  }
  .slider__btns {
    flex: 1;
    min-height: 1px;
    display: flex;
  }
  .btn--link {
    padding-left: 50px;
    background: $blue url('../assets/img/icons/link.svg') no-repeat 15px 50%;
  }
  .slider__descr-btns .btn {
    margin-bottom: 30px;
  }
  .slider__btn {
    width: 50%;
    position: relative;
    img {
      display: block;
      height: 100%;
      width: auto;
      margin: auto;
    }
  }
  .slider__btn-left::after, .slider__btn-right::after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    cursor: pointer;
  }
  .slider__btn-left::after {
    background: rgba($grey, .7) url('../assets/img/icons/portf_arrow_down.svg') no-repeat 50% 50%;
  }
  .slider__btn-right::after {
    background: rgba($grey, .7) url('../assets/img/icons/portf_arrow_up.svg') no-repeat 50% 50%;
  }
  .works {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 80px;
    .title--small {
      &::before, &::after {
        display: none;
      }
    }
    @include laptop {
      justify-content: space-between;
    }
    @include tablets {
      margin-top: 30px;
      justify-content: space-around;
    }
  }
  .work {
    margin-bottom: 50px;
    padding-top: 30px;
    @include tablets {
      margin-bottom: 30px;
      .slider__descr-title {
        margin-bottom: 0;
      }
      .slider__descr-btns .btn {
        margin-bottom: 20px;
      }
      .title {
        margin-bottom: 15px;
      }
    }
  }
  .work:hover {
    box-shadow: 0px 2px 30.1px 4.9px rgba(13, 13, 13, 0.08);
  }
</style>
<script>
import Header from '../components/Header.vue'
import TopSection from '../components/TopSection.vue'
import Footer from '../components/Footer.vue'

export default {
  data () {
    return {
      works: [
        {name: 'Mishka', descr: 'HTML, CSS, Vue.js, Node.js', refSite: 'https://shop-mishka.herokuapp.com', refRepo: 'https://github.com/natallip/shop-Mishka'},
        {name: 'LoftSystem', descr: 'Node.js, Express, MongoDB', refSite: 'https://node-loft.herokuapp.com', refRepo: 'https://github.com/natallip/node-hw-5'},
        {name: 'Mr Burger', descr: 'HTML, CSS, Vue.js, Node.js', refSite: 'https://mr-burger.herokuapp.com', refRepo: 'https://github.com/natallip/burger'},
        {name: 'Design', descr: 'HTML, CSS, JS', refSite: 'https://natallip.github.io/design/index.html', refRepo: 'https://github.com/natallip/design'},
        {name: 'Cozy House', descr: 'HTML, CSS, Vue.js', refSite: 'https://cozy-house.herokuapp.com', refRepo: 'https://github.com/natallip/animal-shelter'}
        //{name: 'Mishka', img: '../assets/img/slider/mishka.jpg', descr: 'HTML, CSS, Vue.js, Node.js', refSite: 'https://shop-mishka.herokuapp.com', refRepo: 'https://github.com/natallip/shop-Mishka'},
//        {name: 'LoftSystem', img: '../assets/img/slider/loftsystem.jpg', descr: 'Node.js, Express, MongoDB', refSite: 'https://node-loft.herokuapp.com', refRepo: 'https://github.com/natallip/node-hw-5'},
//        {name: 'Lavpander', img: '../assets/img/slider/work-1.png', descr: 'Vue.js', refSite: 'https://lavpander.ru', refRepo: false}
      ]
    }
  },
  components: {
    Header,
    TopSection,
    Footer
  },
  computed: {
    activeWork () {
      return this.works[0]
    }
  },
  methods: {
    scroll () {
      let element = document.body
      let toTarget = this.$refs.content
      let duration = 1000

      function scrollTo (element, toTarget, duration) {
        if (duration <= 0) return
        let difference = toTarget.getBoundingClientRect().top
        let perTick = difference / duration * 10

        setTimeout(() => {
          element.scrollTop = element.scrollTop + perTick
          scrollTo(element, toTarget, duration - 10)
        }, 10)
      }
      scrollTo(element, toTarget, duration)
    }
  }
}
</script>



