<template lang="pug">
  div
    Header
    TopSection(@scroll="scroll")

    section.section-about
      .container(ref="content")
        .section-about__title
          .title Чем я могу быть вам полезна
        .section-about__text
          .text
            |Больше всего меня привлекает Frontend разработка, но я также знакома и могу решать не сложные задачи на Backend. Но давайте по порядку.
        .section-about__skills
          .skills(ref="skills")
            .skills__block(v-for="(block, i) in skills", :key="i")
              .skills__title {{ block.title }}
              .skills__items
                .skills__item(v-for="(skill, i) in block.items", :key="i")
                  svg.circle(viewBox="0 0 110 110")
                    circle.circle-above(transform="rotate(-90 55 55)", ref="circle")
                  div.skills__text {{ skill }}
    section.section-map
      .map#map(ref="map")
      .section-map__contacts.contacts
        .contacts__title.title КОНТАКТЫ
        .contacts__content
          ul.contacts__items
            li.contacts__item
              img.contacts__icon(src="../assets/img/icons/skype.svg")
              .contacts__text Petkevich Natalia
            li.contacts__item
              img.contacts__icon(src="../assets/img/icons/envelope.svg")
              .contacts__text petkevich.natalli@gmail.com
            li.contacts__item
              img.contacts__icon(src="../assets/img/icons/phone.svg")
              .contacts__text +375 (29) 75 85 340
            li.contacts__item
              img.contacts__icon(src="../assets/img/icons/map_marker.svg")
              .contacts__text г.Гродно, ул.Захарова, д.24
    Footer

</template>
<style lang="scss">
  @import '../assets/sass/vars';
  @import '../assets/sass/mixins';
  .section-about {
    background-color: $light;
    padding-top: 50px;
  }
  .section-about__title {
    text-align: center;
  }
  .section-about__text {
    text-align: center;
    margin-bottom: 50px;
    display: flex;
    justify-content: center;
    @include laptop {
      margin-bottom: 40px;
    }
  }
  .text {
    font-size: 16px;
    color: $dark-blue;
    font-family: 'robotoregular';
    width: 50%;
    @include laptop {
      font-size: 14px;
      width: 80%;
    }
  }
  .section-about__skills {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
    @include laptop {
      margin-bottom: 30px;
    }
  }
  .skills__block {
    margin-bottom: 30px;
    @include laptop {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 40px;
    }
    @include tablets {
      margin-bottom: 0px;
    }
  }
  .skills__title {
    font-size: 28px;
    color: $dark-blue;
    font-family: 'robotomedium';
    margin-bottom: 10px;
    @include laptop {
      font-size: 21px;
      margin-bottom: 15px;
    }
  }
  .skills__items {
    display: flex;
    @include tablets {
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
  .skills__item {
    margin-right: 40px;
    width: 120px;
    height: 120px;
    border: 22px solid #dfdcd5;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    color: $blue;
    font-family: 'robotomedium';
    position: relative;
    @include laptop {
      width: 110px;
      height: 110px;
      border-width: 20px;
    }
    @include tablets {
      margin-right: 0;
      margin-bottom: 20px;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  .skills__text {
    @include laptop {
      font-size: 13px;
    }
  }
  .circle {
    width: 120px;
    height: 120px;
    fill: none;
    stroke-width: 20;
    r: 45;
    cx: 50%;
    cy: 50%;
    position: absolute;
    @include laptop {
      width: 110px;
      height: 110px;
    }
  }
  .circle-above {
    stroke: $blue;
    stroke-dasharray: .0010 282.6;
    transition: all 2s;
    r: 45;
    cx: 50%;
    cy: 50%;
    @include laptop {
      width: 110px;
      height: 110px;
    }
  }
  .circle-above {
    &--move {
      animation: move 3s ease forwards;
    }
  }
  @keyframes move {
    from {
      // stroke-dasharray: 0 282.6;  // 2*r*3.1415
      stroke-dasharray: 0 282.6;  // 2*r*3.1415
    }
    to {
      stroke-dasharray: 282.6 282.6;
    }
  }
  .section-map {
    position: relative;
  }
  .map {
    width: 100%;
    height: 930px;
    @include desktop {
      height: 830px;
    }
    @include laptop {
      height: 650px;
    }
    @include tablets {
      height: 650px;
    }
  }
  .section-map__contacts {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include tablets {
      top: 0;
      transform: translate(-50%, 0%);
    }
  }
  .contacts {
    min-width: 380px;
    padding: 30px 35px;
    background-color: rgba(#fff, .8);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $dark-blue;
    border-radius: 5px;
    @include laptop {
      padding: 25px 30px;
      min-width: 350px;
    }
    @include tablets {
      background-color: #fff;
      max-width: 320px;
    }
  }
  .contacts__title {
    margin-bottom: 30px;
  }
  .contacts__content {
    align-self: stretch;
  }
  .contacts__items {
    display: flex;
    flex-direction: column;
  }
   .contacts__item {
    width: 100%;
    margin-bottom: 20px;
  }
  .contacts__icon {
    max-width: 25px;
    max-height: 25px;
    margin-right: 15px;
    flex-basis: 25px;
  }
</style>
<script>
import Header from '../components/Header.vue'
import TopSection from '../components/TopSection.vue'
import Footer from '../components/Footer.vue'

export default {
  data () {
    return {
      skills: [
        { title: 'Frontend', items: ['HTML5', 'CSS3', 'JavaScript', 'Vue.js'] },
        { title: 'Backend', items: ['Node.js', 'Express', 'MongoDB'] },
        { title: 'WorkFlow', items: ['Git', 'Bitbucket', 'Gulp', 'Webpack'] }
      ]
    }
  },
  components: {
    Header,
    TopSection,
    Footer
  },
  methods: {
    animateSkills () {
      let skills = this.$refs.skills
      let skillsCoordsTop = skills.getBoundingClientRect().top

      if (skillsCoordsTop <= 300) {
        let circles = this.$refs.circle
        circles.forEach(item => {
          item.classList.add('circle-above--move')
        })
      }
    },
    parallaxScroll () {
      const parallax = function () {
        const bg = this.$refs.bg
        const user = this.$refs.block
        const sectionText = this.$refs.title

        return {
          move: function (block, windowScroll, strafeAmount) {
            let strafe = windowScroll / -strafeAmount + '%'
            let transformString = 'translate3d(0, ' + strafe + ', 0)'
            let style = block.style

            style.transform = transformString
            style.webkitTransform = transformString
          },
          init: function (wScroll) {
            this.move(bg, wScroll, 45)
            this.move(sectionText, wScroll, 20)
            this.move(user, wScroll, 3)
          }
        }
      }
      window.onscroll = function () {
        let wScroll = window.pageYOffset
        parallax.init(wScroll)
      }
    },
    scroll () {
      let element = document.body
      let toTarget = this.$refs.content
      let duration = 1000

      function scrollTo (element, toTarget, duration) {
        if (duration <= 0) return
        let difference = toTarget.getBoundingClientRect().top
        let perTick = difference / duration * 10

        setTimeout(() => {
          element.scrollTop = element.scrollTop + perTick
          scrollTo(element, toTarget, duration - 10)
        }, 10)
      }
      scrollTo(element, toTarget, duration)
    },
    initMap () {
      ymaps.ready(init)

      function init () {
        const myMap = new ymaps.Map('map', {
          center: [59.938631, 30.333055],
          zoom: 16
        })
        myMap.behaviors.disable([
          'drag',
          'scrollZoom'
        ])
        myMap.controls
          .remove('geolocationControl')
          .remove('searchControl')
          .remove('trafficControl')
          .remove('rulerControl')

        const myPlacemark = new ymaps.Placemark([59.938631, 30.323055], {
          hintContent: 'Здесь наш магазин!',
          balloonContent: 'Магазин товаров ручной работы.'
        }, {
          preset: 'islands#darkBlueIcon'
          // iconLayout: 'default#image',
          // iconImageHref: 'src/assets/img/map-pin.png',
          // iconImageSize: [67, 100],
          // iconImageOffset: [-3, -42]
        })

        myMap.geoObjects.add(myPlacemark)
      }
    }
  },
  mounted () {
    this.initMap()
    window.addEventListener('scroll', this.animateSkills, true)
  }
}
</script>



