<template lang="pug">
  div.header
    ul.header__socials.socials
      li
        a(href="https://vk.com/natalia_pt").socials__item
          img.socials__icon.socials__icon--vk(src="../assets/img/icons/vk.svg", alt="")
      li
        a(href="https://www.linkedin.com/in/natalia-pt").socials__item
         img.socials__icon.socials__icon--in(src="../assets/img/icons/in.svg", alt="")
    .hamburger(ref="hamburger", @click="toggle")
      .hamburger__line(v-for="n in 3")
      .overlay(ref="overlay")
        nav.overlay__menu
          router-link(v-for="(link, i) in pages", :key="i", tag="li", :to="link.href").overlay__item
            .overlay__link {{ link.text }}

</template>
<style lang="scss">
  @import '../assets/sass/vars';
  @import '../assets/sass/mixins';
  .header {
    position: relative;
    z-index: 2;
  }
  .hamburger {
    position: absolute;
    top: 20px;
    right: 40px;
    z-index: 100;
    cursor: pointer;
    @include tablets {
      top: 10px;
      right: 10px;
    }
  }
  .hamburger__line {
    width: 40px;
    height: 5px;
    background-color: #fff;
    border-radius: 5px;
    margin: 7px 0;
    transition: all 0.5s ease-in-out;
    @include tablets {
      height: 3px;
      width: 26px;
      margin: 4px 0;
    }
  }
  .active {
    .hamburger__line:nth-child(2) {
      opacity: 0;
    }
    .hamburger__line:nth-child(1) {
      transform: translateY(12px) rotate(45deg);
      background: #fff;
      @include tablets {
        transform: translateY(7px) rotate(45deg)
      }
    }
    .hamburger__line:nth-child(3) {
      transform: translateY(-12px) rotate(-45deg);
      background: #fff;
      @include tablets {
        transform: translateY(-7px) rotate(-45deg);
      }
    }
  }
  .header__wrap {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 25px 5px;
    margin: 0 auto;
    @include laptop {
      padding: 10px 0;
      align-items: flex-end;
    }
  }
  .header__socials {
    width: 80px;
    position: absolute;
    top: 20px;
    left: 30px;
    display: flex;
    align-items: flex-end;
    .socials__item:hover {
      cursor: pointer;
      opacity: .8;
    }
    @include tablets {
      top: 10px;
      left: 20px;
    }
  }
  .socials__item {
    @include laptop {
      display: flex;
      align-items: flex-end;
    }
  }
  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    visibility: hidden;
    transition: opacity .35s, visibility .35s, width .35s;
    z-index: 10;

    &:before, &:after {
      content: '';
      display: block;
      background: rgba($dark-blue, .8);
      top: 0;
      width: 50%;
      height: 100%;
      position: absolute;
      transition: all .35s ease;
      z-index: 20;
    }
    &:before {
      left: -55%;
    }
    &:after {
      right: -55%;
    }
  }
  .overlay.open {
    opacity: .9;
    visibility: visible;
    &:before {
      left: 0;
    }
    &:after {
      right: 0;
    }
    .overlay__item {
      animation: fade .5s ease forwards;
      animation-delay: .35s;
      &:nth-of-type(2) {
        animation-delay: .55s;
      }
      &:nth-of-type(3) {
        animation-delay: .75s;
      }
      &:nth-of-type(4) {
        animation-delay: .95s;
      }
    }
  }
  .overlay__menu {
    position: relative;
    height: 70%;
    top: 50%;
    transform: translateY(-50%);
    font-size: 50px;
    font-family: 'bernier_regularregular';
    z-index: 100;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablets {
      font-size: 35px;
    }
  }
  .overlay__item {
    display: block;
    height: 25%;
    min-height: 50px;
    position: relative;
    opacity: 0;
    cursor: pointer;
  }
  .overlay__link {
    position: relative;
    color: $light;
    &:after, &:before {
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      background: rgba($light, .5);
      width: 22px;
      height: 5px;
      z-index: 100;
      opacity: 0;
      transition: all .35s ease;
    }
    &:after {
      right: -60px;
    }
    &:before {
      left: -60px;
    }
    &:hover:after,
    &:focus:after,
    &:active:after {
      right: -40px;
      opacity: 1;
    }
    &:hover:before,
    &:focus:before,
    &:active:before {
      left: -40px;
      opacity: 1;
    }
  }
  @keyframes fade {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

</style>
<script>
export default {
  data () {
    return {
      pages: [
        { href: '/about', text: 'Обо мне' },
        { href: '/works', text: 'Мои работы' }
      ]
    }
  },
  methods: {
    toggle () {
      const hamburger = this.$refs.hamburger
      const overlay = this.$refs.overlay

      hamburger.classList.toggle('active')
      overlay.classList.toggle('open')
    }
  }
}
</script>



