<template lang="pug">
  .parallax-wrap
    .preloader(ref="preloader")
      .preloader__spinner
        .preloader__animation
        .preloader__percents
          .preloader__percents-count(ref="percents")
          .preloader__percents-sign %

    .parallax(ref="parallax")
      // .parallax__layer(v-for="n in 10", :key="n")
        img(:src="getImg(n)", ref="img")
      //.parallax__layer(v-for="n in 10", :key="n")
        img(:src="'../../assets/img/parallax/Layer' + ' ' + (11 - n) + '.png'" alt="")
      .parallax__layer
        img(src="../assets/img/parallax/Layer 10.png" alt="")
      .parallax__layer
        img(src="../assets/img/parallax/Layer 9.png" alt="")
      .parallax__layer
        img(src="../assets/img/parallax/Layer 8.png" alt="")
      .parallax__layer
        img(src="../assets/img/parallax/Layer 7.png" alt="")
      .parallax__layer
        img(src="../assets/img/parallax/Layer 6.png" alt="")
      .parallax__layer
        img(src="../assets/img/parallax/Layer 5.png" alt="")
      .parallax__layer
        img(src="../assets/img/parallax/Layer 4.png" alt="")
      .parallax__layer
        img(src="../assets/img/parallax/Layer 3.png" alt="")
      .parallax__layer
        img(src="../assets/img/parallax/Layer 2.png" alt="")
      .parallax__layer
        img(src="../assets/img/parallax/Layer 1.png" alt="")

    Block-user(:type="'home'")

</template>
<style lang="scss">
  @import '../assets/sass/vars';
  @import '../assets/sass/mixins';
  .preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background: $blue;
    visibility: visible;
    opacity: 1;
    transition: 1s all;
  }
  .preloader__spinner {
    width: 150px;
    height: 150px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .preloader__percents {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    display: flex;
  }
  .preloader__percents-count {
    margin-right: 10px;
  }
  .preloader__animation {
    width: 150px;
    height: 150px;
    position: relative;
    animation: spin 10s linear infinite;
  }
  .preloader__animation, .preloader__animation:before, .preloader__animation:after {
    content: "";
    display: block;
    position: absolute;
    border: 3px solid transparent;
    border-top-color: #fff;
    border-radius: 50%;
  }
  .preloader__animation:before {
    top: 13px;
    left: 13px;
    right: 13px;
    bottom: 13px;
    animation: spin 7s linear infinite;
  }
  .preloader__animation:after {
    top: 5px;
    left: 5px;
    right: 5px;
    bottom: 5px;
    animation: spin 12s linear infinite;
  }
  @-webkit-keyframes spin {
    from {
      transform: rotate(0deg)
    }
    to {
      transform: rotate(360deg)
    }
  }
  @keyframes spin {
    from {
      transform: rotate(0deg)
    }
    to {
      transform: rotate(360deg)
    }
  }
  .done {
    opacity: 0;
    visibility: hidden;
  }
  .parallax-wrap {
    overflow: hidden;
    width: 100%;
    height: 100%;
    position: relative;
  }
  .parallax {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .parallax__layer {
    position: absolute;
    height: 100%;
    width: 2200px;
    left: 50%;
    margin-left: -1100px;
    img {
      position: absolute;
      bottom: 0;
      max-width: 100%;
    }
  }

  @keyframes fipInXl {
    from {
      transform: perspective(400px) rotate3d(1, 0, 0, 90deg);
      animation-timing-function: ease-in;
      opacity: 0;
    }
    40% {
      transform: perspective(400px) rotate3d(1, 0, 0, -20deg);
      animation-timing-function: ease-in;
    }
    60% {
      transform: perspective(400px) rotate3d(1, 0, 0, 10deg);
      opacity: 1;
    }
    80% {
      transform: perspective(400px) rotate3d(1, 0, 0, -5deg);
    }
    to {
      transform: perspective(400px);
    }
  }
  .flipInX {
    backface-visibility: visible !important;
    animation-name: flipInX;
    animation-duration: 2s;
    animation-fill-mode: both;
    animation-delay: 5s;
  }
</style>
<script>
import BlockUser from '../components/Block-user.vue'
export default {
  data () {
    return {
      pages: [
        {href: '/about', text: 'Обо мне'},
        {href: '/works', text: 'Мои работы'}
      ]
    }
  },
  components: {
    BlockUser
  },
  methods: {
    movePreloader () {
      let preloader = this.$refs.preloader
      let images = document.images
      let images_total_count = images.length
      let images_loaded_count = 0
      let percents = this.$refs.percents

      for (let i=0; i<images_total_count; i++) {
        let image_clone = new Image()
        image_clone.onload = images_loaded
        image_clone.onerror = images_loaded
        image_clone.src = images[i].src
      }
      function images_loaded () {
        images_loaded_count++
        percents.innerHTML = Math.ceil(100 / images_total_count * images_loaded_count)

        if (images_loaded_count >= images_total_count) {
          setTimeout(() => {
            if (!preloader.classList.contains('done')) {
              preloader.classList.add('done')
            }
          }, 1000)
        }
      }
    },
    moveLayers (e) {
      const parallaxContainer = this.$refs.parallax
      const layers = parallaxContainer.children

      let initialX = (window.innerWidth / 2) - e.pageX
      let initialY = (window.innerHeight / 2) - e.pageY

      let layersArray = [ ...layers ]
      layersArray.forEach((layer, i) => {
        let divider = i / 100
        let positionX = initialX * divider
        let positionY = initialY * divider
        let bottomPosition = (window.innerHeight / 2) * divider
        let layerStyle = layer.style
        let transformString = `translate3d(${positionX}px, ${positionY}px, 0)`

        layerStyle.transform = transformString
        layerStyle.bottom = `-${bottomPosition}px`
      })
    }
    // getImg (i) {
    // console.log(111, '../../assets/img/parallax/Layer' + '' + 11-i + '.png')
    // return `../../assets/img/parallax/Layer ${11 - i}.png`
    // }
  },
  mounted () {
    window.addEventListener('mousemove', this.moveLayers)
    this.movePreloader()
  }
}
</script>



